"use client";

import React, { useState, useEffect } from 'react';
import { useSearchParams } from 'next/navigation';
import { useAppContext } from '../../lib/AppContext';
import { motion } from 'framer-motion';
import { Bold, Italic, Underline, Highlighter, Save, FileDown, FileText, Type } from 'lucide-react';

// import * as pdfjs from 'pdfjs-dist'; // Remove direct import

import { analyzePdfContent } from '../../lib/services/geminiService';
import type { Suggestion } from '../../lib/types/proofreader';
import SuggestionSidebar from '../../components/proofreader/SuggestionSidebar';
import PdfViewer from '../../components/proofreader/PdfViewer';

// shadcn/ui
import { Button } from "@/components/ui/button";
import { Separator } from "@/components/ui/separator";
import { Progress } from "@/components/ui/progress";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue
} from "@/components/ui/select";

export default function EditorPage() {
  const searchParams = useSearchParams();
  const documentId = searchParams.get('documentId');
  const { getDocumentContent, getDocumentPdfUrl } = useAppContext();

  const [extractedText, setExtractedText] = useState<string>('');
  const [pdfUrl, setPdfUrl] = useState<string | null>(null);
  const [suggestions, setSuggestions] = useState<Suggestion[]>([]);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [selectedSuggestion, setSelectedSuggestion] = useState<Suggestion | null>(null);
  const [pdfjsLib, setPdfjsLib] = useState<any>(null);
  const [isEditMode, setIsEditMode] = useState<boolean>(false);
  const [fontSize, setFontSize] = useState<string>('text-base');

  useEffect(() => {
    // Dynamically import pdfjs-dist on the client-side
    const loadPdfjs = async () => {
      const pdfjs = await import('pdfjs-dist');
      pdfjs.GlobalWorkerOptions.workerSrc = '/pdf.worker.mjs';
      setPdfjsLib(pdfjs);
    };
    loadPdfjs();
  }, []);

  useEffect(() => {
    if (documentId) {
      const content = getDocumentContent(documentId);
      if (content) {
        setExtractedText(content);
        const url = getDocumentPdfUrl(documentId);
        if (url) setPdfUrl(url);
        // Analyze content for suggestions
        const fetchSuggestions = async () => {
          setIsLoading(true);
          setError(null);
          try {
            // Ensure pdfjsLib is loaded before calling analyzePdfContent if it internally uses pdfjs-dist
            // For now, analyzePdfContent doesn't directly use pdfjs-dist, but if it did, we'd await pdfjsLib
            const aiSuggestions = await analyzePdfContent(content);
            setSuggestions(aiSuggestions);
          } catch (e) {
            console.error("Failed to get suggestions from AI:", e);
            setError("Could not retrieve AI suggestions. Ensure your API_KEY is set.");
          } finally {
            setIsLoading(false);
          }
        };
        fetchSuggestions();
      } else {
        setError("Document content not found.");
      }
    } else {
      setError("No document ID provided.");
    }
  }, [documentId, getDocumentContent]);

  const handleApplyEdit = (pageNumber: number, originalText: string, newText: string) => {
    setExtractedText((prevText) => {
      const pageIdentifier = `--- PAGE ${pageNumber} ---\n`;
      const parts = prevText.split(pageIdentifier);
      if (parts.length > 1) {
        const beforePage = parts[0];
        const pageContentAndAfter = parts.slice(1).join(pageIdentifier);

        const pageContentParts = pageContentAndAfter.split(originalText);
        if (pageContentParts.length > 1) {
          const newPageContent = pageContentParts[0] + newText + pageContentParts.slice(1).join(originalText);
          return beforePage + pageIdentifier + newPageContent;
        }
      }
      return prevText;
    });
    setSuggestions((prevSuggestions) => prevSuggestions.filter(s => !(s.page === pageNumber && s.original === originalText)));
    setSelectedSuggestion(null);
  };

  // Formatting helpers for Edit Mode (simple execCommand fallbacks)
  const applyFormat = (command: 'bold' | 'italic' | 'underline') => {
    try {
      document.execCommand(command);
    } catch {
      // noop
    }
  };

  const applyHighlight = () => {
    try {
      document.execCommand('backColor', false, '#fef08a'); // tailwind yellow-200
    } catch {
      // noop
    }
  };

  const handleExportPDF = () => {
    // Simple fallback: open print dialog; users can save as PDF
    window.print();
  };

  const handleExportDocx = () => {
    // Lightweight .doc export by wrapping HTML in a Word-compatible MIME
    const content = document.getElementById('editable-pane')?.innerHTML || extractedText.replace(/\n/g, '<br/>');
    const header = `
      <html xmlns:o="urn:schemas-microsoft-com:office:office"
            xmlns:w="urn:schemas-microsoft-com:office:word"
            xmlns="http://www.w3.org/TR/REC-html40">
      <head><meta charset="utf-8"><title>Document</title></head><body>`;
    const footer = `</body></html>`;
    const sourceHTML = header + content + footer;
    const blob = new Blob(['\ufeff', sourceHTML], { type: 'application/msword' });
    const url = URL.createObjectURL(blob);
    const link = document.createElement('a');
    link.href = url;
    link.download = 'document.doc';
    document.body.appendChild(link);
    link.click();
    document.body.removeChild(link);
    URL.revokeObjectURL(url);
  };

  if (isLoading) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center px-6">
        <Card className="w-full max-w-md border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900">
          <CardHeader>
            <CardTitle className="text-gray-900 dark:text-gray-50">Loading Document‚Ä¶</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Progress value={66} />
            <p className="text-sm text-gray-600 dark:text-gray-400">Fetching suggestions and preparing the editor.</p>
          </CardContent>
        </Card>
      </div>
    );
  }

  if (error) {
    return (
      <div className="flex flex-col items-center justify-center h-screen bg-zinc-50 dark:bg-zinc-950 text-center text-red-600 dark:text-red-400">
        <h2 className="text-2xl font-semibold">Error: {error}</h2>
        <p className="text-gray-600 dark:text-gray-400 mt-2">Please go back and try uploading again.</p>
      </div>
    );
  }

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
      className="min-h-screen bg-zinc-50 dark:bg-zinc-950 p-4 sm:p-6 lg:p-8 flex justify-center"
    >
      <div className="max-w-none w-full bg-white dark:bg-zinc-900 p-4 sm:p-6 rounded-2xl shadow-xl border border-zinc-200 dark:border-zinc-800 flex flex-col gap-4">
        <div className="flex items-center justify-between">
          <h1 className="text-xl sm:text-2xl font-semibold text-gray-900 dark:text-gray-50">Smart Compliance Editor</h1>
          <div className="flex items-center gap-2">
            <Button variant={isEditMode ? "secondary" : "default"} onClick={() => setIsEditMode(v => !v)}>
              <span className="mr-2">Toggle Edit</span> üìù
            </Button>
            <Button variant="outline" onClick={() => console.log('save clicked')}>
              <Save className="h-4 w-4 mr-2" /> Save
            </Button>
            <Button variant="outline" onClick={handleExportPDF}>
              <FileDown className="h-4 w-4 mr-2" /> Export as PDF
            </Button>
            <Button variant="outline" onClick={handleExportDocx}>
              <FileText className="h-4 w-4 mr-2" /> Export as DOCX
            </Button>
          </div>
        </div>

        {isEditMode && (
          <motion.div
            initial={{ opacity: 0, y: -6 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.25 }}
            className="rounded-lg border border-zinc-200 dark:border-zinc-800 bg-zinc-50/60 dark:bg-zinc-900/60 p-3"
          >
            <div className="flex flex-wrap items-center gap-2">
              <Button size="sm" variant="ghost" onClick={() => applyFormat('bold')}>
                <Bold className="h-4 w-4" />
              </Button>
              <Button size="sm" variant="ghost" onClick={() => applyFormat('italic')}>
                <Italic className="h-4 w-4" />
              </Button>
              <Button size="sm" variant="ghost" onClick={() => applyFormat('underline')}>
                <Underline className="h-4 w-4" />
              </Button>
              <Button size="sm" variant="ghost" onClick={applyHighlight}>
                <Highlighter className="h-4 w-4" />
              </Button>
              <Separator orientation="vertical" className="h-6" />
              <div className="flex items-center gap-2">
                <Type className="h-4 w-4 text-zinc-500" />
                <Select value={fontSize} onValueChange={(v) => setFontSize(v)}>
                  <SelectTrigger className="h-8 w-[120px]">
                    <SelectValue placeholder="Font size" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="text-sm">Small</SelectItem>
                    <SelectItem value="text-base">Normal</SelectItem>
                    <SelectItem value="text-lg">Large</SelectItem>
                    <SelectItem value="text-xl">XL</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
          </motion.div>
        )}

        <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
          <div className="md:col-span-2 rounded-xl border border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900 p-4">
            {!isEditMode ? (
              <PdfViewer
                extractedText={extractedText}
                suggestions={suggestions}
                selectedSuggestion={selectedSuggestion}
                onSuggestionClick={setSelectedSuggestion}
                onApplyEdit={handleApplyEdit}
                pdfUrl={pdfUrl}
              />
            ) : (
              <div
                id="editable-pane"
                className={`prose prose-zinc dark:prose-invert max-w-none outline-none ${fontSize}`}
                contentEditable
                suppressContentEditableWarning
                dangerouslySetInnerHTML={{
                  __html: (() => {
                    const text = extractedText
                      .split('\n')
                      .map(line => line === '' ? '<br/>' : line)
                      .join('<br/>');
                    if (!selectedSuggestion) return text;
                    const { original } = selectedSuggestion;
                    // naive highlight: first occurrence
                    return text.replace(
                      original,
                      `<mark class="bg-yellow-200 dark:bg-yellow-300/40">${original}</mark>`
                    );
                  })()
                }}
              />
            )}
          </div>
          <div className="md:col-span-1 rounded-xl border border-zinc-200 dark:border-zinc-800 bg-white dark:bg-zinc-900 p-3">
            <SuggestionSidebar
              suggestions={suggestions}
              onSuggestionClick={setSelectedSuggestion}
              selectedSuggestion={selectedSuggestion}
              onEditPage={(pageNum) => {
                const suggestionToHighlight = suggestions.find(s => s.page === pageNum);
                if (suggestionToHighlight) {
                  setSelectedSuggestion(suggestionToHighlight);
                }
              }}
            />
          </div>
        </div>
      </div>
    </motion.div>
  );
}
